import * as vscode from 'vscode';
import { SymbolItemNavigation } from './references-view';
import { ContextKey } from './utils';

export class Navigation {

	private readonly a: vscode.Disposable[] = [];
	private readonly b = new ContextKey<boolean>('references-view.canNavigate');

	private c?: SymbolItemNavigation<unknown>;

	constructor(private readonly d: vscode.TreeView<unknown>) {
		this.a.push(
			vscode.commands.registerCommand('references-view.next', () => this.next(false)),
			vscode.commands.registerCommand('references-view.prev', () => this.previous(false)),
			d.onDidChangeSelection(() => this.e()),
		);
	}

	dispose(): void {
		vscode.Disposable.from(...this.a).dispose();
	}

	update(delegate: SymbolItemNavigation<unknown> | undefined) {
		this.c = delegate;
		this.b.set(Boolean(this.c));
	}

	private e(): void {
		if (this.d.selection.length === 0) {
			return;
		}
		const [item] = this.d.selection;
		const location = this.c?.location(item);
		if (!location) {
			return;
		}
		if (vscode.window.activeTextEditor?.document.uri.toString() !== location.uri.toString()) {
			this.g(location, true);
		}
	}

	private f(): undefined | unknown {
		if (!this.c) {
			return undefined;
		}
		const [sel] = this.d.selection;
		if (sel) {
			return sel;
		}
		if (!vscode.window.activeTextEditor) {
			return undefined;
		}
		return this.c.nearest(vscode.window.activeTextEditor.document.uri, vscode.window.activeTextEditor.selection.active);
	}

	private g(loc: vscode.Location, preserveFocus: boolean) {
		vscode.commands.executeCommand('vscode.open', loc.uri, {
			selection: new vscode.Selection(loc.range.start, loc.range.start),
			preserveFocus
		});
	}

	previous(preserveFocus: boolean): void {
		if (!this.c) {
			return;
		}
		const item = this.f();
		if (!item) {
			return;
		}
		const newItem = this.c.previous(item);
		const newLocation = this.c.location(newItem);
		if (newLocation) {
			this.d.reveal(newItem, { select: true, focus: true });
			this.g(newLocation, preserveFocus);
		}
	}

	next(preserveFocus: boolean): void {
		if (!this.c) {
			return;
		}
		const item = this.f();
		if (!item) {
			return;
		}
		const newItem = this.c.next(item);
		const newLocation = this.c.location(newItem);
		if (newLocation) {
			this.d.reveal(newItem, { select: true, focus: true });
			this.g(newLocation, preserveFocus);
		}
	}
}
