import * as vscode from 'vscode';
import { SymbolItemEditorHighlights } from './references-view';

export class EditorHighlights<T> {

	private readonly a = vscode.window.createTextEditorDecorationType({
		backgroundColor: new vscode.ThemeColor('editor.findMatchHighlightBackground'),
		rangeBehavior: vscode.DecorationRangeBehavior.ClosedClosed,
		overviewRulerLane: vscode.OverviewRulerLane.Center,
		overviewRulerColor: new vscode.ThemeColor('editor.findMatchHighlightBackground'),
	});

	private readonly b: vscode.Disposable[] = [];
	private readonly c = new Set<string>();

	constructor(private readonly d: vscode.TreeView<T>, private readonly f: SymbolItemEditorHighlights<T>) {
		this.b.push(
			vscode.workspace.onDidChangeTextDocument(e => this.c.add(e.document.uri.toString())),
			vscode.window.onDidChangeActiveTextEditor(() => d.visible && this.update()),
			d.onDidChangeVisibility(e => e.visible ? this.g() : this.h()),
			d.onDidChangeSelection(() => d.visible && this.update())
		);
		this.g();
	}

	dispose() {
		vscode.Disposable.from(...this.b).dispose();
		for (const editor of vscode.window.visibleTextEditors) {
			editor.setDecorations(this.a, []);
		}
	}

	private g(): void {
		const { activeTextEditor: editor } = vscode.window;
		if (!editor || !editor.viewColumn) {
			return;
		}
		if (this.c.has(editor.document.uri.toString())) {
			return;
		}
		const [anchor] = this.d.selection;
		if (!anchor) {
			return;
		}
		const ranges = this.f.getEditorHighlights(anchor, editor.document.uri);
		if (ranges) {
			editor.setDecorations(this.a, ranges);
		}
	}

	private h(): void {
		for (const editor of vscode.window.visibleTextEditors) {
			editor.setDecorations(this.a, []);
		}
	}

	update(): void {
		this.h();
		this.g();
	}
}
